.baslik{
    @include header();
    margin-top: 8vh;
}

.row{
    margin-top: 29vh;
    margin-right: 0 !important;
    margin-left: 0 !important;
}

.baslikk{
    @include header();
    margin-top: 17vh;
}

.containerr{
    @include flexRow();
    font-family: $textFontFamily !important;
    color: $textColor;
    text-align: justify;
    margin-top: 5vh;
    @include middle();

    .firstBox {
        width: 60vh;
        @include middle();

        .hakkÄ±mdaTitle{
            color: $mainTextColor !important;
            font-weight: bolder !important;
            font-size: $headerTextFontSize;
        }

        .aboutText{
            font-family: $textFontFamily;
        }

        .imageFeature{
            width: 25.7vh;
            margin-top: 1vh;
            border-radius: 2vh;
            @include middle();

            .download{
                width: 100%;
                border: 1px solid $headerColor;
                border-radius: 1vh;
                text-align: center !important;
                margin-top: 0.8vh;
                font-family: $textFontFamily;
                text-decoration: none;
                color: $textColor !important;
                background-color: $bodyColor;
            }

            .download:hover{
                background-color:$headerColor !important;
                color: white !important;
            }
        }



        .skills{
            @include middle();
            p{
                text-align: justify;
                margin-left: 1vh;
                color: $bodyColor;

                small{
                    float: right;
                    margin-right: 1vh;
                }
            }
            .skillsFront{
                background-color: $frontendColor;
                width: 42vh;
                border-radius: 1.2vh;
            }

            .skillsBack{
                background-color: $backendColor;
                width: 37vh;
                border-radius: 1.2vh;
            }

            .skillsData{
                background-color: $databaseColor;
                width: 42vh;
                border-radius: 1.2vh;
            }

            .skillsYapay{
                background-color: $AIColor;
                width: 42vh;
                border-radius: 1.2vh;
            }
        }
    }

}

@media only screen and (max-width:414px){
    .firstBox {
        width: 41.5vh !important;
    }
    .secondText{
        text-align: center !important;
    }
    .hakkÄ±mdaTitle, .title{
        font-size: 2.5vh !important;
    }
    .mainText{
        margin-top: 20vh !important;
        font-size: 7vh !important;
    }
}


@media only screen and (max-width:823px){
    .containerr{
        width: 70vh !important;
        font-size: 2vh !important;
    }
    .hakkÄ±mdaTitle{
        font-size: 2.3vh !important;
    }
    .btn{
        font-size: 2vh;
    }
}

@media only screen and (max-width:1030px){
    .firstBox {
        width: 41.5vh !important;
    }
    .secondText{
        text-align: center !important;
    }
    .mainText{
        margin-top: 20vh !important;
    }
}

@media only screen and (max-width:316px){
    .firstBox, .skillsFront, .skillsBack, .skillsData, .skillsYapay {
        width: 35vh !important;
    }
    .secondText{
        text-align: center !important;
    }
}

@media only screen and (max-width:1199px){
    .merhaba{
            margin-top: 4.5vh;
        }
}

@media only screen and (max-width:1499px){
    .containerr{
        font-size: 2vh !important;
    }
    .merhaba{
            margin-top: 2.5vh;
        }
}