$headerColor:#77868C;
$bodyColor: #f4f4f4;
$mainTextColor: #3E606F;
$cardColor: #566873;
$textColor: #193441;
$mainBackground: #F0F4F3;
$backendColor: #050259;
$frontendColor: #147c69;
$languageColor: #40881f;
$databaseColor: #7C05F2;
$AIColor: #eb9bd3;
$headerFontFamily: 'Dancing Script';
$textFontFamily: 'Red Hat Display';
$mainFontFamily: 'Audiowide';
$headerTextFontSize: 2vh;
$textFont: 1.5vh;
$textFontSize: 5vh;
$mediaFontSize: 3.5vh;
$mainTextFontSize: 10vh;


@mixin flexColumn {
    display:flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-evenly;
}

@mixin flexRow {
    display:flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
}

@mixin header {
    color:$mainTextColor;
    font-family: $textFontFamily;
    font-size: $textFontSize;
    margin-top: $textFontSize;
    text-transform: uppercase;
    @include flexRow();
}

@mixin middle{
    margin-left: auto;
    margin-right: auto;
}

@mixin text {
    margin-top: $headerTextFontSize;
    @include middle();
    color:$textColor;
    font-family: $textFontFamily;
}

@mixin mainBox(){
    text-align: justify !important;
    font-family: $textFontFamily;
    background-color: $bodyColor;
}

