body{
    background-color: $bodyColor;
}

.main{
    .mainText{
        text-align: center;
        color:$mainTextColor;
        font-size: $mainTextFontSize;
        margin-top: 27vh;
        @include flexColumn();
        font-weight: bolder;
        text-shadow: 3px 3px 0px $headerColor ; 
        font-family: $mainFontFamily;
    }
    .centerArrow{
        width: 100%;
        display: flex;
        align-items: center;
        .arrow{
            font-size: 6.5vh;
            color:$mainTextColor;
            margin-top: 10vh;
            @include middle();
        }
    }
}

hr.main{
    border-top: 0.2vh solid $headerColor;
}

.secondText{
    color:$textColor;
    @include middle();
    @include flexRow();
    margin-top: $textFontSize;
    font-family: $textFontFamily;
    font-size: $headerTextFontSize;
}

.mainIcon{
    margin-top: 5vh;
}
