.main-timeline5 {
	overflow: hidden;
	position: relative;
	.timeline {
		position: relative;
		margin-top: -76px;
		&:first-child {
			margin-top: 0;
		}
		&:after {
			content: "";
			display: block;
			width: 100%;
			clear: both;
		}
		&:before {
			content: "";
			display: block;
			width: 100%;
			clear: both;
			content: "";
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			right: 0;
			z-index: 0;
		}
		&:nth-child(2n) {
			.timeline-icon {
				transform: rotate(-135deg);
				border-top-color: $AIColor;
				border-right-color: $AIColor;
			}
			.year {
				transform: rotate(135deg);
				color: $AIColor;
			}
			.timeline-content {
				float: left;
				background: $AIColor;
				&:before {
					left: auto;
					right: -20%;
					background: $AIColor;
				}
			}
		}
		&:nth-child(3n) {
			.timeline-icon {
				border-top-color: $AIColor;
				border-right-color: $AIColor;
			}
			.year {
				color: $AIColor;
			}
			.timeline-content {
				background: $AIColor;
				&:before {
					background: $AIColor;
				}
			}
		}
		&:nth-child(4n) {
			.timeline-icon {
				border-top-color: $frontendColor;
				border-right-color: $frontendColor;
			}
			.year {
				color: $frontendColor;
			}
			.timeline-content {
				background: $frontendColor;
				&:before {
					background: $frontendColor;
				}
			}
		}
		&:nth-child(5n) {
			.timeline-icon {
				border-top-color: $languageColor;
				border-right-color: $languageColor;
			}
			.year {
				color: $languageColor;
			}
			.timeline-content {
				background: $languageColor;
				&:before {
					background: $languageColor;
				}
			}
		}
		&:nth-child(6n) {
			.timeline-icon {
				border-top-color: $AIColor;
				border-right-color: $AIColor;
			}
			.year {
				color: $AIColor;
			}
			.timeline-content {
				background: $AIColor;
				&:before {
					background: $AIColor;
				}
			}
		}
		&:nth-child(7n) {
			.timeline-icon {
				border-top-color: $languageColor;
				border-right-color: $languageColor;
			}
			.year {
				color: $languageColor;
			}
			.timeline-content {
				background: $languageColor;
				&:before {
					background: $languageColor;
				}
			}
		}
		&:nth-child(8n) {
			.timeline-icon {
				border-top-color: $frontendColor;
				border-right-color: $frontendColor;
			}
			.year {
				color: $frontendColor;
			}
			.timeline-content {
				background: $frontendColor;
				&:before {
					background: $frontendColor;
				}
			}
		}
		&:nth-child(9n) {
			.timeline-icon {
				border-top-color: $languageColor;
				border-right-color: $languageColor;
			}
			.year {
				color: $languageColor;
			}
			.timeline-content {
				background: $languageColor;
				&:before {
					background: $languageColor;
				}
			}
		}
		&:nth-child(10n) {
			.timeline-icon {
				border-top-color: $frontendColor;
				border-right-color: $frontendColor;
			}
			.year {
				color: $frontendColor;
			}
			.timeline-content {
				background: $frontendColor;
				&:before {
					background: $frontendColor;
				}
			}
		}
		&:nth-child(11n) {
			.timeline-icon {
				border-top-color: $databaseColor;
				border-right-color: $databaseColor;
			}
			.year {
				color: $databaseColor;
			}
			.timeline-content {
				background: $databaseColor;
				&:before {
					background: $databaseColor;
				}
			}
		}
		&:nth-child(12n) {
			.timeline-icon {
				border-top-color: $languageColor;
				border-right-color: $languageColor;
			}
			.year {
				color: $languageColor;
			}
			.timeline-content {
				background: $languageColor;
				&:before {
					background: $languageColor;
				}
			}
		}
		&:nth-child(13n) {
			.timeline-icon {
				border-top-color: $backendColor;
				border-right-color: $backendColor;
			}
			.year {
				color: $backendColor;
			}
			.timeline-content {
				background: $backendColor;
				&:before {
					background: $backendColor;
				}
			}
		}
	}
	.timeline-icon {
		margin: auto;
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		width: 210px;
		height: 210px;
		border-radius: 50%;
		border: 25px solid transparent;
		border-top-color: $frontendColor;
		border-right-color: $frontendColor;
		z-index: 0;
		transform: rotate(45deg);
	}
	.year {
		margin: auto;
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		display: block;
		width: 110px;
		height: 110px;
		line-height: 110px;
		border-radius: 50%;
		background-color: $mainBackground;
		box-shadow: 0 0 20px rgba(0,0,0,.4);
		font-size: 15px;
		font-weight: 700;
		color: $frontendColor;
		text-align: center;
		transform: rotate(-45deg);
		font-family: $textFontFamily;
	}
	.timeline-content {
		width: 35%;
		float: right;
		background: $frontendColor;
		padding: 30px 20px;
		margin: 50px 0;
		z-index: 0;
		position: relative;
		&:before {
			content: "";
			width: 20%;
			height: 15px;
			background: $frontendColor;
			position: absolute;
			top: 50%;
			left: -20%;
			z-index: 0;
			transform: translateY(-50%);
		}
	}
	.title {
		font-size: 2.5vh;
		font-family: $mainFontFamily;
		font-weight: bolder;
		color: $bodyColor;
		margin: 0 0 10px;
	}
	.description {
		font-family: $textFontFamily;
		font-size: $headerTextFontSize;
		color: $bodyColor;
		line-height: 24px;
		margin: 0;

		.github{
			margin-top: 1vh;
		}
	}
}
@media only screen and (max-width:1199px) and (min-width:1024px){
	.main-timeline5 {
		.timeline {
			margin-top: -75px;
			&:nth-child(2n) {
				.timeline-content {
					&:before {
						right: -18%;
					}
				}
			}
		}
		.timeline-content {
			&:before {
				left: -18%;
			}
		}
	}
}

@media only screen and (max-width:823px){
	.main-timeline5 {
		.timeline {
			margin-top: -5px !important;
			&:nth-child(2n) {
				.timeline-content {
					&:before {
						right: -18%;
					}
				}
			}
		}
		.timeline-content {
			&:before {
				left: -18%;
			}
		}
	}
}
@media only screen and (max-width:992px){
	.main-timeline5 {
		.timeline {
			margin-top: -129px;
			&:nth-child(2n) {
				.timeline-content {
					&:before {
						right: -2%;
					}
				}
			}
		}
		.timeline-content {
			&:before {
				left: -2%;
			}
		}
	}
}

@media only screen and (max-width:768px) {
	.main-timeline5 {
		.timeline {
			margin-top: 0;
			overflow: hidden;
			&:before {
				box-shadow: none;
			}
			&:nth-child(2n) {
				&:before {
					box-shadow: none;
				}
				.timeline-icon {
					margin-top: -30px;
					margin-bottom: 20px;
					position: relative;
					transform: rotate(135deg);
				}
				.year {
					transform: rotate(-135deg);
				}
				.timeline-content {
					width: 100%;
					float: none;
					border-radius: 0 0 20px 20px;
					text-align: center;
					padding: 25px 20px;
					margin: 0 auto;
					&:before {
						width: 15px;
						height: 25px;
						position: absolute;
						top: -22px;
						left: 50%;
						z-index: 0;
						transform: translate(-50%,0);
					}
				}
			}
		}
		.timeline-icon {
			margin-top: -30px;
			margin-bottom: 20px;
			position: relative;
			transform: rotate(135deg);
		}
		.year {
			transform: rotate(-135deg);
		}
		.timeline-content {
			width: 100%;
			float: none;
			border-radius: 0 0 20px 20px;
			text-align: center;
			padding: 25px 20px;
			margin: 0 auto;
			&:before {
				width: 15px;
				height: 25px;
				position: absolute;
				top: -22px;
				left: 50%;
				z-index: 0;
				transform: translate(-50%,0);
			}
		}
	}
}

.portText{
	color:$textColor;
	font-family: $textFontFamily;
	font-size: $headerTextFontSize;
	margin-top: $textFont;

}

.roww{
	margin-top: $mediaFontSize;
}

#ana_div {
    @include middle();
}

.div {
    display: inline-block;
    width: 17vh;
	margin: 1vh;
	font-size: 2.3vh;
	margin-top: 2vh;
	font-family: $headerFontFamily;
	font-weight: bolder !important;

	.backend{
		color:$backendColor;
	}

	.frontend{
		color:$frontendColor;
	}

	.language{
